'use client';

import { useRef, useEffect } from 'react';
import Image from 'next/image';
import { Send } from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import { ChatMessage } from '@/lib/types';

interface ChatInterfaceProps {
  messages: ChatMessage[];
  input: string;
  handleInputChange: (e: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLTextAreaElement>) => void;
  handleSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
  isLoading: boolean;
  className?: string;
  isDarkMode?: boolean; // isDarkMode is no longer used but kept for prop compatibility
}

const ChatInterface: React.FC<ChatInterfaceProps> = ({
  messages,
  input,
  handleInputChange,
  handleSubmit,
  isLoading,
  className = '',
}) => {
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  const handleKeyDown = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (event.key === 'Enter' && !event.shiftKey) {
      event.preventDefault();
      const form = event.currentTarget.form;
      if (form) {
        // Create a native form submission event
        const submitEvent = new Event('submit', { bubbles: true, cancelable: true });
        form.dispatchEvent(submitEvent);
      }
    }
  };

  return (
    <div className={`flex flex-col h-full max-h-screen bg-white rounded-4xl ${className}`}>
      {/* Header */}
      <div className="flex items-center gap-4 px-6 py-4 rounded-t-4xl border-b border-sakura-gray">
        <div className="w-12 h-12 bg-gradient-to-br from-sakura-pink to-sakura-dark-pink rounded-full flex-shrink-0 relative overflow-hidden ring-4 ring-sakura-white">
          <Image
            src="/assets/images/avataaars.png"
            alt="Cayla Avatar"
            fill
            sizes="48px"
            style={{ objectFit: 'cover', transform: 'scale(1.1) translateY(5px)' }}
            priority
          />
        </div>
        <div>
          <h1 className="text-lg font-bold text-sakura-text">Chat with Cayla</h1>
          <p className="text-sm text-sakura-text/70">Your private AI guide for emotional clarity</p>
        </div>
      </div>

      {/* Messages */}
      <div ref={scrollRef} className="flex-1 overflow-y-auto p-6 space-y-5">
        {messages.map((m) => (
          <div key={m.id} className={`flex items-end gap-3 ${m.sender === 'user' ? 'justify-end' : 'justify-start'}`}>
            {m.sender === 'bot' && (
              <div className="w-9 h-9 rounded-full bg-gradient-to-br from-sakura-pink to-sakura-dark-pink flex-shrink-0 self-start">
                <Image src="/assets/images/avataaars.png" alt="Cayla Avatar" width={36} height={36} className="rounded-full" style={{ transform: 'scale(1.1) translateY(2px)' }}/>
              </div>
            )}
            <div className={`px-4 py-2.5 rounded-2xl max-w-lg shadow-sm prose ${m.sender === 'user' 
              ? 'bg-sakura-accent text-white rounded-br-none' 
              : 'bg-sakura-gray text-sakura-text rounded-bl-none'
            }`}>
              <ReactMarkdown>{m.message}</ReactMarkdown>
            </div>
          </div>
        ))}
        {isLoading && (
          <div className="flex items-end gap-3 justify-start">
            <div className="w-9 h-9 rounded-full bg-gradient-to-br from-sakura-pink to-sakura-dark-pink flex-shrink-0">
              <Image src="/assets/images/avataaars.png" alt="Cayla Avatar" width={36} height={36} className="rounded-full" style={{ transform: 'scale(1.1) translateY(2px)' }} />
            </div>
            <div className="p-4 rounded-2xl max-w-md shadow-sm bg-sakura-gray text-sakura-text rounded-bl-none">
              <div className="flex items-center space-x-1.5">
                <div className="w-2 h-2 bg-sakura-pink rounded-full animate-pulse delay-75"></div>
                <div className="w-2 h-2 bg-sakura-pink rounded-full animate-pulse delay-150"></div>
                <div className="w-2 h-2 bg-sakura-pink rounded-full animate-pulse delay-300"></div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Input Form */}
      <div className="p-4 border-t border-sakura-gray bg-white rounded-b-4xl">
        <form onSubmit={handleSubmit} className="flex items-center gap-3">
          <textarea
            value={input}
            onChange={handleInputChange}
            onKeyDown={handleKeyDown}
            placeholder="Type your message, Cayla is listening..."
            rows={1}
            className="flex-1 p-3 bg-sakura-gray rounded-lg resize-none focus:outline-none focus:ring-2 focus:ring-sakura-accent transition-all text-sakura-text placeholder:text-sakura-text/50"
          />
          <button
            type="submit"
            disabled={isLoading || !input.trim()}
            className="w-10 h-10 bg-sakura-accent text-white rounded-lg flex items-center justify-center transition-all duration-300 transform hover:scale-105 disabled:opacity-50 disabled:scale-100 disabled:cursor-not-allowed shadow-md hover:shadow-pink-glow"
          >
            <Send size={18} />
          </button>
        </form>
      </div>
    </div>
  );
};

export default ChatInterface;